@import "vars";

body.login {
  background-color: #fefcff;
  div#login {
    h1 {
      a {

      }
    }

    form#loginform {
      p {
        label {}
        &.forgetmenot {
          line-height: 33px;
          input#rememberme {
            height: 16px;
            width: 16px;
          }
        }
        &.submit {
          input#wp-submit {
            padding: 10px 20px;
            color: #ffffff;
            border: none;
            background: linear-gradient(180deg, #FEBA36 0%, #F92387 163.64%);
            border-radius: 51px;
            font-weight: 500;
            box-shadow: 0 1px 0 #ccc;
            text-shadow: none;
            text-transform: uppercase;
            opacity: 0.8;
            transition: all 200ms ease-in;
            &:hover {
              opacity: 1;
            }
          }
        }
      }

      input {
        padding: 5px 15px;
        background: #FFFFFF;
        border: 1px solid #FC6763;
        border-radius: 15px;
        line-height: 1;
        height: auto;
        box-sizing: border-box;
        &#user_login {}
        &#user_pass {}
      }


    }

    p#nav {
      a {}
    }

    p#backtoblog {
      a {}
    }
  }


  p.message {
    border-color: $d-purple;
  }
}
